body,html {height:100%;overflow:auto;}

.logo{
    padding: 20px;
    font-style: italic;
    color: aliceblue;
    padding-left: 18%;
}
.Home{
    padding: 15px;
    
}
.flex-logo-home{
    display: flex;
    justify-content: space-between;
    padding-bottom: 0px;
}


.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background-color: rgb(45, 47, 71) ;
    width: 300px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    transition:  850ms;
    
}

.nav-text {
    display: flex;
    justify-content:flex-start;
    align-items: center;
    list-style: none;
    height: 80px; 
}


.nav-text a { 
    text-decoration:  none;
    color: aliceblue;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: #67587a;
}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    background-color:rgb(45, 47, 71) ;
    width: 0%;
    height: 80px;
    display: flex;
    justify-content:flex-start;
    align-items: center;
}

span{
    margin-left: 16px;;
}
